type Element = {
  attributes: {
    title?: string;
  };
};

export const createRows = (
  elements: Element[],
  isDesktop: boolean,
  setColumns: (columns: any) => void,
  _columnCount = 2
) => {
  const columnCount = isDesktop ? _columnCount : 1;
  const columns = elements.reduce((_columns, { attributes: project }, i) => {
    if (!project.title) return _columns;

    const columnIndex = i % columnCount;

    _columns[columnIndex] = [..._columns[columnIndex], project];
    return _columns;
  }, Array(columnCount).fill([]));

  setColumns(columns);
};
