import * as React from 'react';

import { Link, navigate } from 'gatsby';

import { ImageType } from '../interfaces/image.interface';
import classNames from 'classnames/bind';
import * as styles from './projectListItem.module.scss';
import Image from './image';

const cx = classNames.bind(styles);

type ImageResponse = {
  data: [
    {
      attributes: ImageType;
    }
  ];
};

type Project = {
  title: string;
  slug: string;
  images: ImageResponse;
};

type Props = {
  project: Project;
};

const ProjectListItem: React.FC<Props> = ({
  project: { title, slug, images },
}) => {
  return (
    <li className={cx('projectStyle')}>
      {images.data[0] && (
        <Image
          className={cx('imageStyle')}
          image={images.data[0].attributes}
          alt={title}
          onClick={() => navigate(`/projects/${slug}`)}
        />
      )}
      <Link className={cx('linkStyle')} to={`/projects/${slug}`}>
        {title}
      </Link>
    </li>
  );
};

export default ProjectListItem;
