import React, { useEffect, useState } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { useBreakpoints } from '../hooks/breakpoints';

import SEO from '../components/seo';

import classNames from 'classnames/bind';
import * as styles from './index.module.scss';

import ProjectListItem from '../components/projectListItem';
import Carousel from '../components/carousel';
import { createRows } from '../utils/row-helper';

const cx = classNames.bind(styles);

const IndexPage: React.FC = () => {
  const data = useStaticQuery<GatsbyTypes.ProjectsQuery>(graphql`
    query Projects {
      mpapi {
        projects(sort: "placementNumber", pagination: { limit: 100 }) {
          data {
            attributes {
              images(pagination: { limit: 1 }) {
                data {
                  attributes {
                    alternativeText
                    height
                    width
                    url
                    formats
                    hash
                  }
                }
              }
              title
              slug
              featured
              featuredPhoto {
                data {
                  attributes {
                    alternativeText
                    formats
                    hash
                    height
                    url
                    width
                  }
                }
              }
            }
          }
        }
      }
    }
  `);

  if (!data.mpapi.projects) return null;

  const [columns, setColumns] = useState([]);
  const isDesktop = useBreakpoints();

  useEffect(() => {
    createRows(data.mpapi.projects.data, isDesktop, setColumns);
  }, [isDesktop]);

  const featuredPhotos = data.mpapi.projects.data.reduce(
    (featuredPhotos, { attributes: { featured, featuredPhoto } }) => {
      if (!featured) return featuredPhotos;
      return [...featuredPhotos, featuredPhoto.data];
    },
    []
  );

  return (
    <>
      <SEO
        title={'Projects | Atelier Mendgen Peissl'}
        description="Architects Anna Mendgen und Dirk Peissl founded Atelier Mendgen Peissl in Berlin in 2012. Find their projects here!"
        image="https://mendgenpeissl.com/logo.jpg"
      />

      {featuredPhotos.length > 0 && isDesktop && (
        <section className={cx('featuredPhotos')}>
          <Carousel images={featuredPhotos} />
        </section>
      )}

      <h1>Projects</h1>

      <section className={cx('projects')}>
        {columns.map((projects, i) => (
          <ul className={cx('projectList')} key={i}>
            {projects.map((project) => (
              <ProjectListItem key={project.slug} project={project} />
            ))}
          </ul>
        ))}
      </section>
    </>
  );
};

export default IndexPage;
