import React, { useState, useRef } from 'react';
import { useIntersection } from '../utils/images-helper';

import {
  ImageType,
  ImageFileWithWidthMap,
} from '../interfaces/image.interface';

import classNames from 'classnames/bind';
import * as styles from './image.module.scss';

const cx = classNames.bind(styles);

type Props = {
  image: ImageType;
  onClick?: () => void;
  className?: string;
  imgClassName?: string;
  alt?: string;
};

const Image: React.FC<Props> = ({
  onClick,
  className,
  image,
  alt,
  imgClassName,
}) => {
  const [loaded, setLoaded] = useState(false);

  const transparentImageUrl =
    'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII=';

  const [isInView, setIsInView] = useState(false);
  const imgRef = useRef();
  useIntersection(imgRef, () => {
    setIsInView(true);
  });

  const widthMap = {
    thumbnail: {
      width: '245',
      minWidth: '0',
      maxWidth: '245',
    },
    small: {
      width: '500',
      minWidth: '246',
      maxWidth: '600',
    },
    medium: {
      width: '750',
      minWidth: '601',
      maxWidth: '800',
    },
    large: {
      width: '1000',
      minWidth: '801',
      maxWidth: '1200',
    },
  };

  const formats: ImageFileWithWidthMap[] = image.formats
    ? Object.keys(image.formats).map((format) => {
        return { ...widthMap[format], ...image.formats[format] };
      })
    : [];

  return (
    <div
      ref={imgRef}
      className={cx(className, 'wrapper', { isLoaded: loaded })}
      onClick={onClick}
    >
      <div
        className={cx('placeholder')}
        style={{
          paddingTop: `${(image.height / image.width) * 100}%`,
        }}
      />

      {isInView && (
        <>
          <img
            className={cx(imgClassName, 'thumbnail')}
            src={image.formats?.thumbnail?.url || transparentImageUrl}
            alt={image.alternativeText || alt || image.hash}
          />

          <picture>
            {formats.map(({ hash, url, minWidth, maxWidth }) => (
              <source
                key={hash}
                srcSet={url}
                media={`(min-width: ${minWidth}px) and (max-width: ${maxWidth}px)`}
              />
            ))}
            <img
              className={cx(imgClassName, 'image')}
              alt={image.alternativeText || alt || image.hash}
              src={image.url}
              onLoad={() => setLoaded(true)}
            />
          </picture>
        </>
      )}
    </div>
  );
};

export default Image;
