import React, { useState, useRef, useEffect } from 'react';

import classNames from 'classnames/bind';
import * as styles from './carousel.module.scss';

const cx = classNames.bind(styles);

import Image from './image';

type Props = {
  images?: GatsbyTypes.ProjectQuery.strapiProject.images;
  className?: string;
};

const Carousel: React.FC<Props> = ({ images }) => {
  if (!images || !(images.length > 0)) return null;

  const carouselElement = useRef<HTMLDivElement>();
  const [imageIndex, _setImageIndex] = useState(0);

  const imagesCount = images.length;

  const setImageIndex = (newImageIndex: number) => {
    let index = Math.min(
      Math.max(Math.round(newImageIndex), 0),
      imagesCount - 1
    );

    if (newImageIndex >= imagesCount) index = 0;

    _setImageIndex(index);
  };

  const handleNext = () => setImageIndex(imageIndex + 1);

  useEffect(() => {
    const timer = setTimeout(() => {
      handleNext();
    }, 5000);

    return () => {
      clearTimeout(timer);
    };
  });

  return (
    <div className={cx('carousel')}>
      <div className={cx('carouselContainer')}>
        <div className={cx('sliderContainer')} ref={carouselElement}>
          {images.map(({ attributes: image }, i) => (
            <div
              key={image.hash}
              className={cx('imageContainer')}
              style={{
                opacity: i === imageIndex ? 1 : 0,
                zIndex: i === imageIndex ? 2 : 1,
              }}
            >
              <Image
                className={cx('imageWrapper')}
                imgClassName={cx('image')}
                image={image}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Carousel;
